import axios from 'axios'
import { ref } from 'vue'
import { showConsoleError } from '@/functions/errors'
import { APIS_DRIVE } from '@/constants/apis'
// const TIMEOUT = 10000


export async function postHeatMapRAJANT(params:any) {
  
  /**
   * Estado de la Peticin AXIOS
   */
  const statusAxios = ref()
  
  await axios
    .get(APIS_DRIVE.value.postHeatMapRAJANT,{ params: params })
    .then((response) => {
        const finalData:any = response.data.body;
        const finalObj = ref<any[]>([])
        const showGraphs = ref(finalData.length > 0)
        if(finalData.length > 0){
            finalObj.value = finalData
        }
      statusAxios.value = { status: 200, data : finalObj.value, showGraphs : showGraphs.value }
    })
    .catch(function (error) {
      statusAxios.value = { status: 400, data : [], showGraphs : false }
      showConsoleError(error)
    })
  return statusAxios.value
}

